.paper {
  background-color: #fff;
  border-radius: 50px;
  box-sizing: border-box;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 20px;
  padding-bottom: 50px;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 100px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  box-shadow: 4px 4px 10px gray;
}

.title {
  font-size: 36px;
  margin-top: 10px;
  margin-bottom: 20px;
}

.divider {
  margin-top: 0px;
  margin-bottom: 50px;
  width: 260px;
  height: 12px;
}
