.container {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 30px;
}

@media (max-width: 1500px) {
  .container {
    grid-template-columns: 1fr 1fr;
  }
}

.item {
  max-width: 240px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  align-self: start;
  justify-self: center;
}

.thumbnail {
  width: 240px;
  height: 180px;
  margin-bottom: 15px;
}

.title {
  font-size: 20px;
  margin-bottom: 2px;
  margin-top: 0px;
  text-align: center;
}

.description {
  margin-top: 5px;
  font-size: 16px;
  color: #555;
}

@media (max-width: 950px) {
  /* .container {
    grid-template-columns: 1fr;
  } */
  .item {
    max-width: 150px;
  }

  .thumbnail {
    width: 150px;
    height: 100px;
  }

  .title {
    font-size: 18px;
  }

  .description {
    font-size: 12px;
  }
}
