.container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 100%;
}

.item {
  max-width: 280px;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.logo {
  width: 160px;
  height: 160px;
}

.title {
  font-size: 24px;
  margin-top: 20px;
  margin-bottom: 10px;
}

.description {
  font-size: 16px;
  margin-top: 10px;
  margin-bottom: 10px;
  color: #555;
}
