.name {
  font-size: 80px;
  margin-bottom: 0px;
  margin-top: 0;
}

.occupation {
  font-size: 40px;
  margin-top: 10px;
  margin-bottom: 50px;
}

.scrollArea {
  position: absolute;
  top: 0;
  left: 35vw;
  font-size: 100px;
  height: 100vh;
  width: 65vw;
  overflow: scroll;
}

@media (max-width: 950px) {
  .scrollArea {
    width: 80vw;
    left: 20vw;
  }
}

@media (max-width: 500px) {
  .scrollArea {
    width: 100vw;
    left: 0;
  }

  .name {
    font-size: 50px;
    margin-left: 10px;
  }

  .occupation {
    font-size: 30px;
    margin-left: 10px;
  }
}
